import "./App.css";
import ToDoList from "./TodoList";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { useState } from "react";
// import ToDo from "./Todo";

function App() {
  library.add(fab, far, fas);
  const title = `Doable`;
  const [tasks, setTasks] = useState([]);
  const [taskName, setTaskName] = useState("");

  const completeTask = (id) => {
    tasks[id - 1].completed = true;
    document.querySelectorAll(".todo")[id - 1].classList.add("todo-completed");
    console.log(tasks, document.getElementsByClassName("taskDescriptionCompleted")[id - 1]);
    setTasks(tasks);
  };

  const handleChange = (e) => {
    setTaskName(e.target.value);
  };

  const handleSubmit = (task, e) => {
    e.preventDefault();
    if (!task) return;
    addTask(task);
    setTaskName("");
  };

  const addTask = (task) => {
    const newTask = {
      task: task,
      completed: false,
      id: tasks.length + 1,
    };
    let newTasks = [...tasks, newTask];
    console.log(newTasks);
    setTasks(newTasks);
  };
  const deleteTask = (id) => {
    const newTasks = tasks.filter((task) => task.id !== id);
    setTasks(newTasks);
  };
  return (
    <div className="App">
      <div className="content">
        <h1 className="app-title">{title}</h1>
        <form className="toDoForm" action="" onSubmit={(e) => handleSubmit(taskName, e)}>
          <label htmlFor="makeToDo" className="makeToDoLabel">
            Whatcha gotta do?
          </label>
          <input type="text" className="makeToDo" placeholder="Name your task here." onChange={handleChange} />
          <button className="createToDo">Create Task</button>
        </form>
        <div className="task-container">
          <ToDoList tasks={tasks} deleteTask={deleteTask} addTask={addTask} completeTask={completeTask} />
        </div>
      </div>
    </div>
  );
}
export default App;
